<h1 id="切换到管理员状态" tabindex="-1"><a class="header-anchor" href="#切换到管理员状态" aria-hidden="true">#</a> 切换到管理员状态</h1>
<p>以下的操作都是基于<code>Windows</code>，如果你使用其他系统，请自行解决</p>
<p>安装<code>Win</code>和<code>X</code>键，然后选择管理员终端即可</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211210201040357" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211210201040357.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>然后选择<code>Windows终端 (管理员)</code>即可</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211210201202994" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211210201202994.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
